.Poll{

}

.PollContainer{
    position: relative;
    display: flex;

    min-width: 483px;
    min-height: 400px;

    width: 1300px;
    height: 900px;

    top: 50%;
    left: 50%;

    background-color: khaki;

    justify-content: center;
    align-items: center;
    
    transform: translate(-50%, -50%);
}



@media screen and (max-width: 1300px)
{
    .PollContainer{
        position: relative;

        width: 100vw;
        height: 100%;
        
        transform: translate(-50%, -50%);
    
        top: 50%;
        left: 50%;

        background-color: khaki;
    }
}

