.SliderContainer{
    position: absolute;

    min-width: 483px;
    min-height: 400px;

    width: 1200px;
    height: 550px;
    
    transform: translate(-50%, -50%);

    top: 50%;
    left: 50%;
}

.SliderCard{
    position: absolute;

    width: 100%;
    height: 100%;

    background-image: url(image/GameExhibit.png);
    background-size:cover;
    background-repeat: no-repeat;

    pointer-events: auto;
}

.SliderCard a{
    z-index: inherit;
}

.SliderCard a.gameName{
    position: absolute;
    pointer-events: none;

    font-size: 30px;

    left: 10%;
    top: 15%;
}

.SliderCard a.context{
    position: absolute;
    pointer-events: none;

    font-size: 20px;

    left: 10%;
    top: 30%;
}

.SliderCard a.icon{
    position: absolute;

    width: 100px;
    height: 100px;

    left: 80%;
    top: 70%;
}

.SliderCard img{
    width: 100px;
    height: 100px;

    left: auto;
    top: auto;
}

@media screen and (max-width: 1300px)
{
    .SliderContainer{
        position: absolute;

        width: 100vw;
        height: 660px;
        
        transform: translate(-50%, -50%);
    
        top: 50%;
        left: 50%;
    }
}


@media screen and (max-width: 1000px)
{
    .SliderContainer{
        position: absolute;

        width: 100vw;
        height: 660px;
        
        transform: translate(-50%, -50%);
    
        top: 50%;
        left: 50%;
    }
}