.PollContent{
    position: relative;
    display: flex;
    
    align-items: center;
    justify-content: center;
    text-align: center;

    padding: 10px;

    height:inherit;

}

.PollContent div{
    display: grid;
}

.PollContent a{
    position: relative;

}
.PollContent img{
    position: relative;

    width: 300px;
    height: 300px;
}

.Info:not(.PollContainer){
    position: absolute;
    display: flex;

    align-items: center;
    justify-content: center;

    left: 4%;

    top:20%;
}

.Info a{
    font-size: 30px;
    font-style: bold;
}

.Info button{
    margin-left: 10px;
}

.PollContent button{
    position: relative;

    width: 50%;

    transform: translate(50%, 0%);
}