.background
{
    background-color: black;
}

.main{
    position: relative;

    display: flex;

    max-width: 1920px;
    min-width: 483px;

    min-height: 900px;

    width: auto;
    height: auto;

    align-items: center;
    justify-content: center;

    transform: translate(0%, 60px);
}

.main div.GameInfo{
    position: relative;

    max-width: 1920px;
    min-width: 483px;

    min-height: 900px;

    width: 1600px;
    height: inherit;
}

.main div.Vote{
    position: relative;

    max-width: 1920px;
    min-width: 483px;

    min-height: 900px;
    max-height: 1024px;

    width: inherit;
    height: inherit;

    display: none;
}

.LeftDeco{
    position: absolute;

    width: 300px;
    height: 650px;

    top: 57%;
    left: 5%;
    transform: translate(0%, -50%);
}

.RightDeco{
    position: absolute;

    width: 300px;
    height: 650px;

    top: 57%;
    right: 5%;
    transform: translate(0%, -50%);
}

.RightButton{
    position: absolute;

    width: 20px;
    height: 20px;

    top: 50%;

    transform: translate(50%, 50%);

}

.LeftButton{
    position: absolute;

    width: 20px;
    height: 20px;
}

@media screen and (max-width: 1200px)
{
    .main div.GameInfo{
        position: relative;
    
        max-width: 1920px;
        min-width: 483px;
    
        min-height: 900px;
        
        width: inherit;
        height: inherit;
    
    }
}
